
import { computed, defineComponent, ref } from 'vue';
import Steps from '@/components/Steps.vue';
import Quagga from '@ericblade/quagga2';
import { ApiRequest, API_URLS } from '@/api/request.api';

export default defineComponent({
  name: 'Wareneingang',
  components: { Steps },
  setup() {
    const step = ref(1);
    const steps = ref(2);
    const item = ref({
      amount: 0,
      good_till: '1992-10-30',
      product_name: '',
      product_code: '',
      product_price: '',
      old_good_till: '1992-10-30',
      old_amount: 0,
      price_tag: 0,
      is_gram: false,
    });
    const codeFound = ref(false);
    const quagging = ref(false);
    const fetching = ref(false);
    const inventoryItemFoundId = ref('');

    const missingValues = computed(() => {
      return inventoryItemFoundId.value !== ''
        ? !item.value.amount || !item.value.good_till
        : !item.value.product_name ||
            !item.value.price_tag ||
            !item.value.amount ||
            !item.value.good_till;
    });

    const nextStep = async () => {
      if (!item.value.product_code) return;
      const req = await new ApiRequest(
        `${API_URLS.SEARCH_ITEM}?filter[product_code][_eq]=${item.value.product_code}`
      )
        .get()
        .execute();
      if (req.data.length === 1) {
        // eslint-disable-next-line prefer-destructuring
        item.value = req.data[0];
      }

      step.value += 1;
    };

    const previousStep = () => {
      item.value = {
        amount: 0,
        good_till: '1992-10-30',
        product_name: '',
        product_code: '',
        product_price: '',
        old_good_till: '1992-10-30',
        old_amount: 0,
        price_tag: 0,
        is_gram: false,
      };
      step.value = 1;
    };

    const startScanning = () => {
      quagging.value = true;
      Quagga.init(
        {
          locate: true,
          frequency: 5,
          numOfWorkers: 2,
          locator: { halfSample: true, patchSize: 'medium' },
          inputStream: {
            name: 'Live',
            type: 'LiveStream',
            target: document.getElementById('code-scanner') || undefined,
          },
          decoder: {
            readers: ['ean_reader'],
          },
        },
        // eslint-disable-next-line func-names
        function (err) {
          if (err) {
            throw new Error(err);
          }
          Quagga.start();
        }
      );
    };

    const stopScanning = () => {
      quagging.value = false;
      Quagga.stop();
    };

    Quagga.onProcessed((data) => {
      if (data && data.codeResult && data.codeResult.code) {
        codeFound.value = true;
      }
    });

    Quagga.onDetected((data) => {
      item.value.product_code = data.codeResult.code
        ? data.codeResult.code
        : '';
      quagging.value = false;
      Quagga.stop();
    });

    const newProduct = () => {
      item.value = {
        amount: 0,
        good_till: '1992-10-30',
        product_name: '',
        product_code: '',
        product_price: '',
        old_good_till: '1992-10-30',
        old_amount: 0,
        price_tag: 0,
        is_gram: false,
      };
      step.value = 1;
    };

    const addProduct = async () => {
      fetching.value = true;
      if (inventoryItemFoundId.value === '') {
        try {
          await new ApiRequest(API_URLS.ADD_NEW_INVENTORY_ENTRY)
            .post()
            .body({ ...item.value })
            .execute();
          step.value += 1;
        } catch (error) {
          newProduct();
          throw new Error(error);
        } finally {
          fetching.value = false;
        }
      } else {
        await new ApiRequest(
          `${API_URLS.UPDATE_ITEM}/${inventoryItemFoundId.value}`
        )
          .patch()
          .body({
            ...item.value,
          })
          .execute()
          .catch((err) => {
            newProduct();
            throw new Error(err);
          });
        step.value += 1;
        fetching.value = false;
      }
    };

    return {
      startScanning,
      stopScanning,
      item,
      quagging,
      codeFound,
      step,
      steps,
      nextStep,
      previousStep,
      missingValues,
      addProduct,
      newProduct,
      fetching,
      inventoryItemFoundId,
    };
  },
});
